}

/* swiper-pagination  */
.swiper-pagination {
  position: static;
  width: auto !important;
}
.swiper-pagination-bullet {
  position: static;
  border-radius: 2px;
  width: 8px;
  height: 8px;
  background: #191A2A;
}

.swiper-number {
  width: 52px;
  height: 52px;
  border-radius: 100px;
  font-weight: 700;
  font-size: 24px;
  line-height: 110%;
  color: #fff;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease-in;
}

.swiper-pagination-fraction {
  font-weight: 700;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.5);
}
.swiper-pagination-fraction .swiper-pagination-current {
  font-weight: 700;
  font-size: 26px;
  color: var(--belyy);
}

.swiper-scrollbar {
  position: static !important;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
}
.swiper-scrollbar-drag {
  background: var(--belyy);
  border-radius: 10px;
}

.swiper-scrollbar-temnyy {
  position: static !important;
  background: var(--izobrazhenie);
}
.swiper-scrollbar-temnyy .swiper-scrollbar-drag {
  background: var(--color-zelenyy);
}

@media (max-width: 768px) {
  .swiper-number {
    flex-grow: 1;
    height: 44px;
    font-weight: 700;
    font-size: 24px;
    line-height: 110%;
    color: #fff;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease-in;
  }
}
@keyframes transformItem {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.transform-item {
  opacity: 0;
}

.scale-item {
  scale: 0.6;
  opacity: 0;
  transition: scale 0.4s ease, opacity 0.3s ease;
}

@keyframes textAnimation {
  0% {
    opacity: 0;
    transform: translateX(-120px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
.text-animation {
  opacity: 0;
}

.animation-active.scale-item {
  opacity: 1;
  scale: 1;
}
.animation-active.scale-item:nth-child(2n) {
  transition: scale 0.6s ease, opacity 0.5s ease;
}
.animation-active.scale-item:nth-child(3n) {
  transition: scale 0.9s ease, opacity 0.8s ease;
}
.animation-active.scale-item:nth-child(5n) {
  transition: scale 0.7s ease, opacity 0.6s ease;
}
.animation-active.scale-item:nth-child(7n) {
  transition: scale 0.8s ease, opacity 0.7s ease;
}
.animation-active.text-animation {
  opacity: 1;
  animation: textAnimation 0.4s forwards;
}
.animation-active.transform-item {
  opacity: 1;
  animation: transformItem 0.4s forwards;
}
.animation-active.transform-item:nth-child(2n) {
  animation: transformItem 0.6s forwards;
}
.animation-active.transform-item:nth-child(3n) {
  animation: transformItem 0.7s forwards;
}
.animation-active.transform-item:nth-child(5n) {
  animation: transformItem 0.8s forwards;
}
.animation-active.transform-item:nth-child(7n) {
  animation: transformItem 0.9s forwards;
}

/* animation photo */
.img-animation img {
  -webkit-transition: scale 3s ease;
  -o-transition: scale 3s ease;
  transition: scale 3s ease;
}

.img-animation {
  overflow: hidden;
  position: relative;
  border-radius: 2px;
}
.img-animation:hover img {
  scale: 1.08;
}
.img-animation::before {
  border-radius: 2px;
  display: block;
  pointer-events: none;
  content: "";
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  /* opacity: 0.5; */
  -webkit-clip-path: circle(0% at 100% 100%);
  clip-path: circle(0% at 100% 100%);
  z-index: 2;
  -webkit-transition: -webkit-clip-path 0.4s ease-in-out;
  transition: -webkit-clip-path 0.4s ease-in-out;
  -o-transition: clip-path 0.4s ease-in-out;
  transition: clip-path 0.4s ease-in-out;
  transition: clip-path 0.4s ease-in-out, -webkit-clip-path 0.4s ease-in-out;
  background: rgba(66, 133, 244, 0.2);
}
.img-animation:hover::before {
  -webkit-clip-path: circle(100%);
  clip-path: circle(100%);
}

/* popups */
.popup {
  height: 100vh;
  left: 50%;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  width: 100%;
  z-index: 1000;
  -webkit-transition: opacity 0.4s ease;
  -o-transition: opacity 0.4s ease;
  transition: opacity 0.4s ease;
  overflow-y: auto;
  background: rgba(0, 0, 0, 0.7);
}

.popup__body {
  width: 100%;
  min-height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  padding: 20px;
}

.popup.open {
  opacity: 1;
  pointer-events: all;
}

.popup__content {
  position: relative;
  width: 100%;
  position: relative;
  -webkit-transition: -webkit-transform 0.3s linear;
  transition: -webkit-transform 0.3s linear;
  -o-transition: transform 0.3s linear;
  transition: transform 0.3s linear;
  transition: transform 0.3s linear, -webkit-transform 0.3s linear;
  -webkit-transform: rotateY(90deg);
  transform: rotateY(90deg);
}

.popup.open .popup__content {
  -webkit-transform: rotateY(0);
  transform: rotateY(0);
}

.popup input {
  width: 100%;
}

.close-popup {
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 1000;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}
.close-popup svg path {
  transition: all 0.3s ease;
}

.close-popup img {
  height: 100%;
}

.close-popup:hover {
  background: #fff;
}
.close-popup:hover svg path {
  fill: var(--osnovnoy);
}

.none {
  display: none;
}

